<template>
	<div>
		<div class="clearfix title-box am-margin-bottom-lg">
			<div class="pull-left">
				<h3 class="title"></h3>
			</div>
			<div class="pull-right">
				<el-breadcrumb separator-class="el-icon-arrow-right">
					<el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
					<el-breadcrumb-item></el-breadcrumb-item>
				</el-breadcrumb>
			</div>
		</div>
		<el-form ref="form" :model="form" label-position="left" label-width="120px">
			<el-form-item label="">
				<el-select v-model="form.cate_1st" placeholder="">
					<el-option v-for="item in cate_1st_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>
				<el-select v-model="form.cate_2nd" placeholder="">
					<el-option v-for="item in cate_2nd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>
				<el-select v-model="form.cate_3rd" placeholder="">
					<el-option v-for="item in cate_3rd_options" :key="item.id" :label="item.name" :value="item.id"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="14">
					<el-input v-model.trim="form.name"></el-input>
				</el-col>
				<el-col :span="24" class="tip">3200</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="14">
					<el-input type="textarea" v-model.trim="form.hotPoint"></el-input>
				</el-col>
				<el-col :span="24" class="tip">140</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim.number="form.price">
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip">0-999999</el-col>
				<el-col :span="24" class="tip"></el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim.number="form.marketPrice">
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip">0-999999</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim.number="form.cost">
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip">0-999999</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model="form.discount" disabled>
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip"></el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim.number="form.inventory">
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip">0-999999</el-col>
				<el-col :span="24" class="tip"></el-col>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim="form.articleNo"></el-input>
				</el-col>
				<el-col :span="24" class="tip"></el-col>
				<el-col :span="24" class="tip">20_/-</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-upload :limit="1" list-type="picture-card" :before-upload="handleBeforeUpload" :on-success="handleMainSuccess" :before-remove="handleMainBeforeRemove" :on-exceed="handleMainExceed" :on-error="handleError" :on-preview="handleCardPreview" action="/api/upload/goods/">
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
				<el-col :span="24" class="tip">jpgifpng 800*8001M</el-col>
			</el-form-item>
			<el-form-item label="">
				<el-upload :limit="6" list-type="picture-card" :before-upload="handleBeforeUpload" :on-success="handleSliderSuccess" :on-exceed="handleSliderExceed" :before-remove="handleSliderBeforeRemove" :on-error="handleError" :on-preview="handleCardPreview" action="/api/upload/slider/">
					<i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
					<img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
				<el-col :span="24" class="tip">jpgifpng 800*8001M</el-col>
			</el-form-item>
			<div class="section-title"></div>
			<el-form-item label="">
				<el-col :span="14">
					<el-input v-model.trim="form.brand"></el-input>
				</el-col>
			</el-form-item>
			<el-form-item label="">
				<div id="toolbar" class="w-e-toolbar"></div>
				<div id="editorZone" ref="editor" class="w-e-text-container"></div>
			</el-form-item>
			<div class="section-title"></div>
			<el-form-item label="">
				<el-select v-model="form.province" placeholder="">
					<el-option label="" value="shanghai"></el-option>
					<el-option label="" value="beijing"></el-option>
					<el-option label="" value="beijing"></el-option>
				</el-select>
				<el-select v-model="form.city" placeholder="">
					<el-option label="" value="shanghai"></el-option>
					<el-option label="" value="beijing"></el-option>
					<el-option label="" value="beijing"></el-option>
				</el-select>
				<el-select v-model="form.area" placeholder="">
					<el-option label="" value="shanghai"></el-option>
					<el-option label="" value="beijing"></el-option>
					<el-option label="" value="beijing"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="">
				<el-col :span="3">
					<el-input v-model.trim="form.freight">
						<template slot="append"></template>
					</el-input>
				</el-col>
				<el-col :span="24" class="tip">0</el-col>
			</el-form-item>
			<el-form-item>
				<el-button type="success" @click="releaseHandle"></el-button>
			</el-form-item>
		</el-form>
	</div>
</template>
<script>
	import E from "wangeditor";
	export default {
		data() {
			return {
				dialogImageUrl: '',
				dialogVisible: false,
				form: {
					cate_1st: "",
					cate_2nd: "",
					cate_3rd: "",
					hotPoint: "",
					name: "",
					price: "",
					marketPrice: "",
					cost: "",
					discount: "0",
					inventory: "",
					articleNo: "",
					img_lg: "",
					img_md: "",
					slider: "",
					brand: "",
					detail: "",
					freight: "0",
					province: "",
					city: "",
					area: "",
				},
				cate_1st_options: [],
				cate_2nd_options: [],
				cate_3rd_options: [],
			};
		},
		created() {
			this.cateChangeHandle(1, 'cate_1st');
		},
		mounted() {
			var editor = new E('#toolbar', '#editorZone');
			editor.customConfig.zIndex = 100
			//
			editor.customConfig.uploadImgServer = '/api/upload/common/';
			editor.customConfig.uploadFileName = 'file';
			//HTMLdata
			editor.customConfig.onchange = html => {
				this.form.detail = html;
			};
			editor.create();
		},
		watch: {
			'form.cate_1st' (newValue, oldValue) {
				this.cateChangeHandle(newValue, 'cate_2nd');
			},
			'form.cate_2nd' (newValue, oldValue) {
				this.cateChangeHandle(newValue, 'cate_3rd');
			},
		},
		methods: {
			//change
			cateChangeHandle(id, cate) {
				if(!id) {
					return false;
				}
				this.getOptions(id).then((data) => {
					//
					this[cate + '_options'] = data;
					//
					if(data.length == 0) {
						this.form[cate] = '';
						return false;
					}
					//
					this.form[cate] = data[0].id;
				});
			},
			//
			getOptions(id) {
				return this.axios.get('/api/category/sub/', {
						params: {
							pId: id
						}
					})
					.then(function(result) {
						if(result.data.status) {
							//
							return Promise.resolve(result.data.data);
						} else {
							this.$message.error(result.data.msg);
						}
					});
			},
			handleMainSuccess(response, file, fileList) {
				if(response.status) {
					this.form.img_lg = response.lgImg;
					this.form.img_md = response.mdImg;
				}
			},
			handleError(err, file, fileList) {
				this.$message.error('' + err);
			},
			handleBeforeUpload(file) {
				let reg = /^image\/(jpe?g|png)$/;
				const isJPG = reg.test(file.type);
				const isLt2M = file.size / 1024 / 1024 < 2;
				if(!isJPG) {
					this.$message.error(' JPG/PNG !');
				}
				if(!isLt2M) {
					this.$message.error(' 2MB!');
				}
				return isJPG && isLt2M;
			},
			handleMainBeforeRemove(file, fileList) {
				if(!file.response) {
					return;
				}
				this.axios
					.post('/api/upload/delete/', {
						src: '.' + file.response.lgImg
					})
					.then((result) => {
						if(result.status) {
							return this.axios.post('/api/upload/delete/', {
								src: '.' + file.response.mdImg
							});
						}
					}).then((result) => {
						if(result.status) {
							//
							this.form.img_lg = '';
							this.form.img_md = '';
							return true;
						} else {
							return false;
						}
					});
			},
			handleCardPreview(file) {
				this.dialogImageUrl = file.url;
				this.dialogVisible = true;
			},
			handleMainExceed(files, fileList) {
				this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `);
			},
			handleSliderSuccess(response, file, fileList) {
				if(response.status) {
					this.form.slider = this.convertFileList(fileList);
				}
			},
			handleSliderExceed(files, fileList) {
				this.$message.warning(` 6  ${files.length}  ${files.length + fileList.length} `);
			},
			handleSliderBeforeRemove(file, fileList) {
				if(!file.response) {
					return;
				}
				this.axios
					.post('/api/upload/delete/', {
						src: '.' + file.response.src
					})
					.then((result) => {
						if(result.status) {
							this.form.slider = this.convertFileList(fileList);
							return true;
						} else {
							return false;
						}
					});
			},
			//
			convertFileList(fileList) {
				let res = [];
				fileList.forEach(function(item, index) {
					res.push(item.response.src);
				});
				return res.toString();
			},
			//
			releaseHandle() {
				this.axios
					.post("/api/goods/release/", this.form)
					.then((result) => {
						if(result.data.status) {
							this.$router.go(0);
							this.$message({
								message: '',
								type: 'success'
							});
						}
					});
			}
		}
	};
</script>
<style scoped>
	.title-box {
		border-bottom: 1px solid #409eff;
	}
	
	.title-box h3 {
		margin-top: 0;
	}
	
	.tip {
		font-size: 12px;
		color: #999;
	}
	
	.section-title {
		background-color: #f5f5f5;
		padding: 10px;
		margin-bottom: 10px;
	}
	
	.el-upload img {
		max-width: 100%;
	}
	
	.w-e-toolbar {
		border: 1px solid #ccc;
		border-bottom: 0;
	}
	
	.w-e-text-container {
		border: 1px solid #ccc;
		min-height: 1500px;
		height: 1500px;
	}
</style>